/* eslint @typescript-eslint/no-unsafe-member-access: 0 */
// TODO event.target„ÇíÂºïÊï∞„Å´ÊåáÂÆö„Åß„Åç„Å™„ÅÑÊñπÊ≥ï„ÇíË™øÊüª„Åô„Çã„ÄÇ‰∏ÄÊó¶ÂïèÈ°å„ÅåÂá∫„Å™„ÅÑ„Åì„Å®„ÅØÁ¢∫Ë™çÊ∏à„Åø„Å™„ÅÆ„Åßany„ÅßÂõûÈÅø„ÄÇ
import { useState, VFC } from 'react';
import { Item } from 'semantic-ui-react';
import { Container, Draggable, DropResult } from 'react-smooth-dnd';
import arrayMove from 'array-move';
import { useDispatch, useSelector } from 'react-redux';
import ResetButton from '../../component/atoms/ResetButton';
import AddButton from '../../component/atoms/AddButton';
import InputForm from '../../component/atoms/InputForm';
import { RootState } from '../../reducer';
import TaskBar from '../../component/molecules/TaskBar';
import { Task } from '../../Data/data';
import SaveButton from '../../component/atoms/SaveButton';
// import SaveButton from '../../component/atoms/SaveButton';

/* ---------„Çø„Çπ„ÇØ„É™„Çπ„ÉàÊú¨‰Ωì„ÅÆ„Ç≥„É≥„Éù„Éº„Éç„É≥„Éà--------- */

/* ----------„Çø„Çπ„ÇØ„ÅÆÂûãÂÆöÁæ©---------- */

type Props = {
  add?: (id: number, taskName: string) => void;
  del?: (index: number) => void;
  edit?: () => void;
  refresh?: (list: Task[]) => void;
};
/* -------------------------------- */

const TaskList: VFC<Props> = ({
  add = () => undefined,
  del = () => undefined,
  edit = () => undefined,
  refresh = () => undefined,
}) => {
  /* ------------Store,Reducer„Å´Èñ¢„Åô„ÇãÂá¶ÁêÜ------------ */
  // Store„Å´Ê†ºÁ¥ç„Åó„Å¶„ÅÇ„Çã„Çø„Çπ„ÇØ„É™„Çπ„Éà„ÅÆÊú¨‰Ωì
  const taskListState = useSelector<RootState, Task[]>(
    (state: RootState) => state.taskList,
  );

  // dispatcher
  const dispatch = useDispatch();

  /* --------Áä∂ÊÖã(state)„ÅÆÂÆöÁæ©------------- */

  // „Çø„Çπ„ÇØÂêç
  const [taskName, setTaskName] = useState<string>('');
  // „Çø„Çπ„ÇØ„ÅÆid
  const [idCnt, setIdCnt] = useState(
    taskListState.length > 0
      ? taskListState[taskListState.length - 1].id + 1
      : 0,
  );
  // ËøΩÂä†„Éú„Çø„É≥„ÅÆÊ¥ªÊÄßÂà∂Âæ°
  const [disabled, setDisabled] = useState(true);

  /* ------------„Çø„Çπ„ÇØ„ÅÆËøΩÂä†„Å´Èñ¢„Åô„ÇãÂá¶ÁêÜ------------ */
  // ---„Çø„Çπ„ÇØ„ÅÆËøΩÂä†
  const addTask = () => {
    const newIdCnt = idCnt + 1;
    setIdCnt(newIdCnt);
    // call reducer
    dispatch(add(newIdCnt, taskName));

    // ÂÖ•Âäõ„Åï„Çå„ÅüÂÄ§„Çí„ÇØ„É™„Ç¢„Éº„Åô„Çã
    setTaskName('');
    setDisabled(true);
  };

  /* ------------ÂÖ•Âäõ„Éï„Ç©„Éº„É†„Å´Èñ¢„Åô„ÇãÂá¶ÁêÜ------------ */
  // ÂÖ•Âäõ„Éï„Ç©„Éº„É†„ÅÆÂÖ•Âäõ„ÇíÂèó‰ªò„Åë„Çã„Åü„ÇÅ„ÅÆÂá¶ÁêÜ
  const handleValue = (target: HTMLInputElement) => {
    // 35ÊñáÂ≠ó‰ª•‰∏ä„ÅÆÂÖ•Âäõ„ÅØ„Çø„Çπ„ÇØ„Åã„Çâ„ÅØ„ÅøÂá∫„Å¶„Åó„Åæ„ÅÜ„ÅÆ„ÅßÂèó„Åë‰ªò„Åë„Å™„ÅÑ
    if (target.value.length > 35) {
      return;
    }
    setTaskName(target !== null ? target.value : '');
    setDisabled(!(target.value.length > 0));
  };

  /* ------------„Çø„Çπ„ÇØ„ÅÆ„É™„Çª„ÉÉ„Éà„Å´Èñ¢„Åô„ÇãÂá¶ÁêÜ------------ */

  // ---ÂÖ®„Å¶„ÅÆ„Çø„Çπ„ÇØ„ÅÆ„É™„Çª„ÉÉ„Éà
  const reset = () => {
    // eslint-disable-next-line no-alert
    const confirmAnswer = window.confirm('Do you wanna delete All Task ?');
    // „Ç≠„É£„É≥„Çª„É´„Åï„Çå„ÅüÂ†¥Âêà„ÅØ„É™„Çª„ÉÉ„Éà„Çí‰∏≠Êñ≠
    if (!confirmAnswer) {
      return;
    }

    // setTask([]);
    dispatch(refresh([]));
  };

  /* ------„Çø„Çπ„ÇØ„Çí„Éâ„É©„ÉÉ„Ç∞&„Éâ„É≠„ÉÉ„Éó„Åß„ÇΩ„Éº„ÉàÂèØËÉΩ„Å´„Åô„Çã„Åü„ÇÅ„ÅÆÈñ¢Êï∞------ */

  const onDrop = (dropResult: DropResult) => {
    // `DropResult` „ÅßÂûãÂÆöÁæ©
    const { removedIndex, addedIndex } = dropResult;
    // setTask((tasksArray) =>
    //   arrayMove(tasksArray, removedIndex || 0, addedIndex || 0),

    let newTaskListState = [...taskListState];
    newTaskListState = arrayMove(
      newTaskListState,
      removedIndex || 0,
      addedIndex || 0,
    );
    dispatch(refresh(newTaskListState));
  };

  /* ------------------------------------------------------ */

  return (
    <>
      <div className="task-list-container">
        <InputForm
          inputValue={taskName}
          placeholder="Enter any task that you have to do."
          onChangeHandler={(e) => handleValue(e.target)}
        />
        <AddButton addTask={addTask} disabled={disabled} />
        <ResetButton reset={reset} />
        <SaveButton />

        <Item.Group>
          <Container
            drag-handle-selector=".drag-handle-selector"
            lockAxis="y"
            onDrop={onDrop}
          >
            {taskListState.map((task, index) => (
              <Draggable key={task.id}>
                <TaskBar
                  targetTask={task}
                  index={index}
                  del={del}
                  edit={edit}
                  refresh={refresh}
                />
              </Draggable>
            ))}
          </Container>
        </Item.Group>
      </div>
    </>
  );
};

export default TaskList;
